	module.exports = {"data":{"post":{"html":"<p>For our new McCaf&eacute; we've found the the perfect promotion team, 16 dynamic, active and athletic women. We now the uniform sponsors for the 1st women's <a href=\"http://www.buchholz08-handball.de/\">handball team of TSV Buchholz 08</a>, who play in the 3rd <a href=\"http://www.hamburgerhv.de/\">Hamburg Handball League</a>. Last weekend we officially presented the uniforms in our Gym and Fun.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2111820033\" title=\"View 'CIMG0640' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2362/2111820033_a55459cf51.jpg\" alt=\"CIMG0640\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>It would have been cool to have shirts in St. Pauli brown, but the supplier could only offer white with brown text. The league is on winter break, but the season resumes in January.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2112590858\" title=\"View 'CIMG0636' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2315/2112590858_8848f87984.jpg\" alt=\"CIMG0636\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2112587038\" title=\"View 'CIMG0634' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2190/2112587038_0602858085.jpg\" alt=\"CIMG0634\" border=\"0\" width=\"500\" height=\"375\" /></a></p>","fields":{"title":"Go Team","date":"15 Dec 2007","url":"https://papascott.de/2007-12-15-go-team/"}}},"pathContext":{"slug":"/2007-12-15-go-team/","prev":{"fields":{"title":"Flying Saucer","date":"14 Dec 2007","slug":"/2007-12-14-flying-saucer/"}},"next":{"fields":{"title":"Monomedia","date":"16 Dec 2007","slug":"/2007-12-16-monomedia/"}}}}

